
import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import Badge from 'vux/src/components/badge/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import OFooter from "@/components/common/OFooter";
export default {
components: {
OFooter,
Badge,
Group,
Cell,
ViewBox,
AlertModule
},
data() {
return {
list: []
};
},
methods: {
getAlertTaskMenu() {
let params = {
params: {
RuleName: "GetAlertTaskMenu",
Parm: { TimePoint: formatDate(new Date(), 'yyyyMMdd') }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.list = this.list.concat(data.RspMsg.result.ListItems.ListItem || []);
}
else {
this.showModule("获取数据失败", data.RspMsg.msg);
}
this.$store.commit('updateLoadingStatus', { isLoading: false })
};
callPuExctQryToOICS(params, callBackSuccess);
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
},//是否启用
isDisabled(menuId) {
let isDisabled = true;
if (menuId == '1303' || menuId == '1306' || menuId == '1308') isDisabled = true
else isDisabled = false
return isDisabled;
},//获取跳转URL和设置参数
getLink(menuId) {
let typeValue = "";
let typeName = "WAPR";
let titleName = "";
let ruleName = "";
let linkName = "";

if (menuId == '1301') {
typeValue = "1";
titleName = "事后监测处理";
ruleName = "GetPostAlertList";
linkName = "warning_processing/processing_details";
}
else if (menuId == '1302') {
typeValue = "2";
titleName = "事中监测处理";
ruleName = "GetPreAlertList";
linkName = "warning_processing/processing_details";
}
else if (menuId == '1304') {
typeValue = "4";
titleName = "发出预警查证通知";
ruleName = "GetRegisteredEventList";
linkName = "";
}
else if (menuId == '1305') {
typeValue = "5";
titleName = "录入预警查证反馈意见";
ruleName = "GetFeedbackEventList";
linkName = "warning_processing/check_feedback";
}
else if (menuId == '1307') {
typeValue = "7";
titleName = "审核预警";
ruleName = "GetReviewEventList";
linkName = "warning_processing/verify_ealywarning";
}
return {
path: "/warning_processing/processing_list", query: { typeValue: typeValue, typeName: typeName, titleName: titleName, ruleName: ruleName, linkName: linkName }
};
},//获取菜单名字
getMenuName(menuId) {
let menuName = "";
if (menuId == '1301') menuName = "事后监测处理"
else if (menuId == '1302') menuName = "事中监测处理"
else if (menuId == '1303') menuName = "定期监测处理"
else if (menuId == '1304') menuName = "发出预警查证通知"
else if (menuId == '1305') menuName = "录入预警查证反馈意见"
else if (menuId == '1306') menuName = "生成预警结论"
else if (menuId == '1307') menuName = "审核预警"
else if (menuId == '1308') menuName = "确认预警"
return menuName;
}
},
mounted() {


},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit('updateLoadingStatus', { isLoading: true })
vm.list = [];
vm.getAlertTaskMenu();
})
}
};



